.header-root {
  background-color: #4f27bf;
  color: white;
  height: 80px;
  padding: 0 32px;

  .header-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 100%;

    .header__button {
      display: flex;
      align-items: center;
      justify-content: space-between;
      outline: none;
      background: none;
      border: none;
      padding: 0;
      color: white;
      cursor: pointer;
      font: inherit;
      font-weight: 700;
    }

    .content-auth {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 217px;
    }
  }
}
